import Vue from 'vue'
import Vuex from 'vuex' 

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    header: {
     	type:0,
     	title:''
    },
    showFooterMenu:true
  },
  // mutations 
  mutations : {
	setHeader (state , {type , title}) {
    if(void 0 != type) {
      state.header.type = type
    }
	 	state.header.title = title ;
	},
	setFooterVisiable (state , isShow) {
		state.showFooterMenu = isShow ;
	}	
  },
  // actions,mutationstate
  //  store.dispatch('testMehtod') actions
  actions : {
  	// 
  	testMehtod ({commit}) {
		  commit('mutationMethod')
  	}
  }
})



